import { APPROVE_ANSWER, SHOW_ASSIGNMENT, UPDATE_ANSWER } from "./assignmentsActionTypes";

const assignments = ["vad gr flickan - hon badar.jpg", "vad gr dom-dom ker hiss.jpg", "vad gr hon-hon ker hiss.jpg", "vad gr dom-dom ker tunnelbana.jpeg", "vad gr pojken - han grter.jpg","vad gr pojken - han skrattar.jpg", "vad gr flickan-hon borstar tnderna.jpg", "vad gr pojken-han springer.gif", "vad gr flickan-hon ter banan.jpg", "vad gr bill callahan-han spelar gitarr.jpg"];

const pickRandom = (a) => {
    return a[Math.floor(Math.random() * a.length)] || "";
};

const nrOfRunsLeft = 40;
const initialState = {
    assignments: assignments,
    currentFilename: pickRandom(assignments),
    previousFilename: "",
    showAssignment: true,
    nrOfRunsLeft: nrOfRunsLeft < assignments.length ?  nrOfRunsLeft: assignments.length,
    currentText: ""
};

export default (state = initialState, action) => {
    switch (action.type) {
        case APPROVE_ANSWER:
            const { assignments, currentFilename, nrOfRunsLeft } = state;
            const undoneAssignments = assignments.filter(assignment => assignment !== currentFilename);
            return {
                ...state,
                assignments: undoneAssignments,
                currentFilename: pickRandom(undoneAssignments),
                nrOfRunsLeft: nrOfRunsLeft - 1,
                showAssignment: false,
                currentText: "",
                previousFilename: currentFilename
            };
        case SHOW_ASSIGNMENT:
            return {
                ...state,
                showAssignment: true
            };
        case UPDATE_ANSWER:
            return {
                ...state,
                currentText: action.writtenText
            };
        default:
            return state
    }
}


